import React from 'react';
const EditableRow =({editFormData,handleEditFormChange,handleCancelClick}) =>{
    return(
       <tr>
           <td>
               <input type="text" required="required" placeholder="Enter name" name="fullname" value={editFormData.fullname} onChange={handleEditFormChange}></input>
           </td>
           <td>
           <input type="text" required="required" placeholder="Enter address" name="address" value={editFormData.address} onChange={handleEditFormChange}></input>
           </td>
           <td>
           <input type="text" required="required" placeholder="Enter phonenumber" name="phonenumber" value={editFormData.phonenumber} onChange={handleEditFormChange}></input>
           </td>
           <td>
           <input type="email" required="required" placeholder="Enter email" name="email"  value={editFormData.email} onChange={handleEditFormChange}></input>
           </td>
           <td>
               <button type="submit">Save</button>
               <button type="button" onClick={handleCancelClick}>Cancel</button>
           </td>
       </tr>
    )
}

export default EditableRow