import React,{useState,Fragment} from "react";
import "../../styles/style.css";
import data from "./mock-data.json";
import {nanoid} from 'nanoid';
import {Form,FloatingLabel} from 'react-bootstrap';
import ReadOnlyRow from "./ReadOnlyRow";
import EditableRow from "./EditableRow";
function Admin(){
    const[employees,setEmployees] = useState(data);
    const [addFormData,setAddFormData]=useState({
        fullname:'',
        address:'',
        phonenumber:'',
        email:''
    })

    const [editFormData,setEditFormData] = useState({
        fullname:'',
        address:'',
        phonenumber:'',
        email:''
    });

    const[editEmployeeId,setEditEmployeeId] = useState(null);

    const handleAddFormChange = (event) =>{
        event.preventDefault();
        const fieldName = event.target.getAttribute('name');
        const fieldValue = event.target.value;
        const newFormData ={...addFormData};
        newFormData[fieldName]=fieldValue;
        setAddFormData(newFormData);
    }

    const handleEditFormChange = (event)=>{
        event.preventDefault();
        const fieldName = event.target.getAttribute('name');
        const fieldValue = event.target.value;
        const newFormData = {...editFormData}
        newFormData[fieldName]=fieldValue;
        setEditFormData(newFormData);
    }

    const handleAddFormSubmit = (event) =>{
        event.preventDefault();
        const newEmployee = {
            id:nanoid(),
            fullname:addFormData.fullname,
            address:addFormData.address,
            phonenumber:addFormData.phonenumber,
            email:addFormData.email
        };
        const newEmployees = [...employees,newEmployee];
        setEmployees(newEmployees);
    }

    const handleEditFormSubmit = (event) =>{
        event.preventDefault();
        const editedEmployee={
            id:editEmployeeId,
            fullname:editFormData.fullname,
            address:editFormData.address,
            phonenumber:editFormData.phonenumber,
            email:editFormData.email

        };
        const newEmployees=[...employees];
        const index=employees.findIndex((employee)=>employee.id === editEmployeeId);
        newEmployees[index] = editedEmployee;
        setEmployees(newEmployees);
        setEditEmployeeId(null);
    }

    const handleEditClick =(event,employee)=>{
        event.preventDefault();
        setEditEmployeeId(employee.id);
        const formValues = {
            fullname:employee.fullname,
            address:employee.address,
            phonenumber:employee.phonenumber,
            email:employee.email,
        }
        setEditFormData(formValues);
    }

    const handleCancelClick=()=>{
        setEditEmployeeId(null);
    }

    const handleDeleteClick=(employeeId)=>{
        const newEmployees =[...employees];
        const index=employees.findIndex((employee)=>employee.id === employeeId);
        newEmployees.splice(index,1);
        setEmployees(newEmployees);
    }

    return(
        <div className="app-container">
        <h3>Hi i am Admin</h3>
        <form onSubmit={handleEditFormSubmit}>
        <table>
            <thead>
                <tr>
                    <th>Name</th>
                    <th>Address</th>
                    <th>Phone number</th>
                    <th>Email</th>
                    <th>Actions</th>
                </tr>
            </thead>
            <tbody>
                {employees.map((employee) =>(
                    <>
                    {editEmployeeId === employee.id ? (
                        <EditableRow editFormData={editFormData} handleEditFormChange={handleEditFormChange}
                        handleCancelClick={handleCancelClick}
                        /> 
                    )  :(
                        <ReadOnlyRow employee={employee} handleEditClick={handleEditClick}
                        handleDeleteClick={handleDeleteClick}
                        />
                    )}
                    </>
                    
                ))}
                
            </tbody>
        </table>
        </form>
        <h2>Add a new Employee</h2>
        <div onSubmit={handleAddFormSubmit}>
        <FloatingLabel controlId="floatindInput" label="Name" className="mb-3">
            <Form.Control type="text" name="fullname" placeholder="John" onChange={handleAddFormChange}/>
        </FloatingLabel>
        <FloatingLabel controlId="floatindInput" label="Address" className="mb-3">
            <Form.Control type="text" name="address" placeholder="madurai" onChange={handleAddFormChange}/>
        </FloatingLabel>
        <FloatingLabel controlId="floatindInput" label="Phone number" className="mb-3">
            <Form.Control type="text" name="phonenumber" placeholder="1234567890" onChange={handleAddFormChange}/>
        </FloatingLabel>
        <FloatingLabel controlId="floatindInput" label="email" className="mb-3">
            <Form.Control type="email" name="email" placeholder="anything@example.com" onChange={handleAddFormChange}/>
        </FloatingLabel>
        <button className="btn btn-primary" type="submit">Add</button>
        </div>
        </div>

    )
}
export default Admin;